@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root {
  max-width: 100%;
  height: 100%;
  font-size: 62.5%;
}
body {
  font-family: Raleway, sans-serif;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}

input, textarea {
  color: #333;
  padding: .8rem;
  font-size: 1.2em;
  border-radius: .4rem;
  border: .1rem solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 4rem auto;
  color: #333;
  font-size: 2em;
}
.loading, .error {
  text-align: center;
  margin: 4rem auto;
}
.searchbar {
  width: 100%;
}
.searchbar form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.searchbar label {
  font-size: 2em;
}

@media screen and (min-width: 768px) {
  .searchbar form {
    flex-direction: row;
    grid-gap: 1rem;
    gap: 1rem;
    width: 80%;
    max-width: 40rem;
  }
}
.navbar {
  background: #58249c;
  padding: 2rem;
  color: #fff;
  height: 100%;
}
.navbar nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 120rem;
  margin: 0 auto;
  grid-gap: 1rem;
  gap: 1rem;
}
.navbar a.brand {
  margin-right: auto;
  color: #fff;
  text-decoration: none;
  font-size: 1.8em;
  width: 100%;
  text-align: center;
}
.navbar a:last-child {
  color: #fff;
  text-decoration: none;
  padding: .8rem;
  border: .1rem solid #fff;
  border-radius: .4rem;
  font-size: 1.8em;
}
.navbar a:last-child:hover {
  background: #fff;
  color: #333;
}


@media screen and (min-width: 768px) {
  .navbar nav {
    flex-direction: row;
    align-items: stretch;
  }
  .navbar a.brand {
    text-align: left;
    width: 60%;
  }
  .navbar a:last-child {
    width: 50%;
    max-width: 13rem;
    text-align: center;
  }
}
.recipe-list{
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 4rem;
  max-width: 120rem;
  margin: 2rem;
}
.recipe-list .card {
  background: #fff;
  color: #333;
  padding: 2rem;
  border-radius: .4rem;
  box-shadow: .3rem .3rem .3rem rgba(0,0,0,0.05);
  transition: all 0.3s ease;
  position: relative;
  font-size: 2rem;
}
.recipe-list .card:hover {
  -webkit-transform: rotate(3deg);
          transform: rotate(3deg);
}
.recipe-list .card .h3 {
  color: #555;
  margin-bottom: .6rem;
}
.recipe-list .card p {
  color: #999;
  font-size: 1em;
}
.recipe-list .card div {
  color: #555;
  font-size: 0.9em;
  margin: 2rem 0;
  line-height: 1.5em;
}
.recipe-list .card a {
  color: #555;
  text-decoration: none;
  display: block;
  background: #e2e2e2;
  font-size: 0.9em;
  text-align: center;
  width: 12rem;
  padding: .8rem;
  border-radius: .4rem;
  margin: 2rem auto 0;
}
.delete {
  position: absolute;
  top: 1rem;
  right: 1rem;
  cursor: pointer;
  -webkit-filter: invert(60%);
          filter: invert(60%)
}

/* dark mode */

.recipe-list .card.dark {
  background: #555
}
.recipe-list .card.dark p,
.recipe-list .card.dark h3,
.recipe-list .card.dark div {
  color: #e4e4e4;
}
.home {
  max-width: 120rem;
  margin: 6rem auto;
}
.create {
  color: #777;
  max-width: 48rem;
  margin: 6rem auto;
}
.create label {
  font-size: 2.2em;
}
.create form {
  padding-bottom: 2rem;
}
.create label span {
  display: block;
  margin: 3rem 0 1rem;
}
.create p {
  margin-top: 1rem;
  font-size: 1.5em;
}
.create button {
  display: block;
  width: 50%;
  font-size: 2em;
  color: #fff;
  padding: .8rem 2rem;
  border: 0;
  border-radius: .4rem;
  background-color: #58249c;
  cursor: pointer;
  text-decoration: none;
  margin: 2rem auto;
}

.create form label button {
  font-size: 1em;
}

.ingredients {
  display: flex;
  align-items: center;
}
.ingredients button {
  margin: 0 0 0 1rem;
}

.recipe {
  max-width: 120rem;
  margin: 4rem auto;
  text-align: center;
  background: #fff;
  padding: 4rem;
  box-sizing: border-box;
  font-size: 2.2rem;
}
.recipe p {
  font-size: 1em;
}
.recipe ul {
  display: flex;
  padding: 0;
  justify-content: center;
}
.recipe .btn {
  background-color: #777;
  border-radius: .8rem;
  padding: 1.2rem;
  margin-top: 1.2rem;
  color: #fff;
  font-size: 1em;
}
.recipe .btn:hover {
  background-color: #fff;
  color: #000;
}
.recipe li {
  list-style-type: none;
  margin-right: 1rem;
  color: #777;
  font-size: .8em;
}
.recipe li::after {
  content: ',';
}
.recipe li:last-child::after {
  content: '.';
}

.method {
  text-align: left;
  line-height: 1.5em;
  font-size: .8em;
  padding-top: 2rem;
}

/* dark mode */
.recipe.dark {
  background: #555;
  color: #e4e4e4;
}
.App {
  min-height: 100%;
}
.App.dark {
  background: #333;
}
.theme-selector {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  max-width: 120rem;
  margin: 2rem auto;
}
.theme-buttons {
  margin-right: 2rem;
}
.theme-buttons div {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  margin-left: 1.5rem;
  border-radius: 50%;
}
.mode-toggle {
  margin-left: 2rem;
  margin-right: auto;
}
.mode-toggle img {
  width: 2.4rem;
  height: 2.4rem;
  cursor: pointer;
}
